.first {
    color: #eef;
    position: relative;
}

.first .title {
    font-size: 4em;
    font-weight: 800;
}

.first .lead {
    font-size: 2em;
    font-weight: 300;
    color: #db6;
}

.first .page-body {
    flex-grow: 1;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center;
}


.social-icon-container {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
}

.social-icon {
    height: 40px !important;
    width: 40px !important;
    margin-right: 15px;
    margin-left: 15px;
}

.social-icon:hover .social-svg-mask{
    fill: #fff !important;
}

#snow-target {
    overflow: hidden;
}





.second {
    background-image: -webkit-linear-gradient(45deg, #FBE5C8, #FFC2C7, #8FDDE7)
}

.second .title {
    font-size: 3em;
    font-weight: 300
}

.second .content {
    font-size: 1.5em;
    font-weight: 400;
}

.second .page-body {
    flex-grow: 1;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center;
}
.card {
    flex-grow: 1;
    min-width: 150px;
    max-width: 30%;
    height: 300px;
    margin: 20px;
    flex-basis: 20%;
    border: none;
    border-color: aliceblue;
    box-sizing: border-box;
    border-radius: 50px;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    overflow: hidden;
    transition: all ease-in-out 0.5s;
    cursor: pointer;
}

.card:hover {
    border: solid 16px rgba(255, 255, 255, 0.787);
}

.card:hover {
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

.image-wrapper {
    flex-grow: 1;
    margin: 5px;
}

.skill-title-wrapper>h4 {
    font-size: 2em;
    font-family: 'Dosis', sans-serif;
    font-weight: 400;
}
.third {
    background-color: #fafafa;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.third .title {
    font-size: 3em;
    font-weight: 300
}

.third {
    justify-content: flex-start;
    align-content: stretch;
}


.card-wrapper {
    align-self: stretch;
    width: inherit;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-content: space-between;
}





.nav-bar {
    position: fixed;
    z-index: 1000;
    top: 0;
    right: 0;
    left: 0;
    background-color: rgba(50,50,50,0.8);
    box-shadow: 0 0 5px #333;
    height: 40px;
    padding-left: 20px;
    padding-right: 20px;
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
}

.nav-link {
    margin-left: 10px;
    margin-right: 10px;
    font-size: 1.2em;
    font-weight: 500 !important;
    color: #eeeeee;
    cursor: pointer;
    border-bottom: solid 2px transparent;
}

.nav-link:hover {
    color: #ec7;
    border-bottom: solid 2px #ec7;
}


.App {
  text-align: center;
  font-family: 'Dosis' , sans-serif;
}

.particles {
  position: fixed;
  top: 0px;
  height: 100vh;
  width: 100vw;
}

hr {
  width: 300px;
  border: solid 1px rgba(10, 10, 10, 0.3);
}



